import React, { useMemo, useState } from 'react'
import { graphql, useStaticQuery } from 'gatsby'
import { css } from '@emotion/react'
import ProjectItem from './ProjectItem'
import Section from '../section/Section'
import { projects } from './data'
import { motion, AnimatePresence, Variants } from 'framer-motion'

const categories = ['All', 'Vue', 'React', 'Mapbox', 'Three']

const ProjectsSection: React.FC = () => {
  const [selectedCategory, setSelectedCategory] = useState('All')

  const data = useStaticQuery(graphql`
    query {
      allFile(
        filter: {
          relativeDirectory: { eq: "portfolio" }
          extension: { regex: "/(jpg|png|jpeg|gif)/i" }
        }
      ) {
        edges {
          node {
            base
            publicURL
          }
        }
      }
    }
  `)

  const images = useMemo(() => {
    const imageMap: { [key: string]: string | undefined } = {}
    data.allFile.edges.forEach(({ node }) => {
      imageMap[node.base] = node.publicURL
    })
    return imageMap
  }, [data])

  const categoryCounts = useMemo(() => {
    const counts: { [key: string]: number } = {}
    categories.forEach(cat => {
      counts[cat] =
        cat === 'All'
          ? projects.length
          : projects.filter(p => p.tech.includes(cat)).length
    })
    return counts
  }, [])

  const filteredProjects = projects.filter(
    project =>
      selectedCategory === 'All' || project.tech.includes(selectedCategory),
  )

  const cardVariants: Variants = {
    hidden: {
      opacity: 0,
      y: 50, //   ()
      scale: 0.9,
    },
    visible: {
      opacity: 1,
      y: 0, //  
      scale: 1,
      transition: {
        duration: 0.6,
        ease: [0.4, 0, 0.2, 1],
      },
    },
    exit: {
      opacity: 0,
      scale: 0.9,
      transition: { duration: 0.3 },
    },
  }

  return (
    <Section title="My Work">
      {/*    */}
      <div className="project-filters">
        {categories.map(category => (
          <button
            key={category}
            onClick={() => setSelectedCategory(category)}
            className={`project-category ${selectedCategory == category ? 'active' : ''}`}
          >
            {category}
            {/*    */}
            <span
              className={`project-category-count ${selectedCategory == category ? 'active' : ''}`}
            >
              {categoryCounts[category]}
            </span>
          </button>
        ))}
      </div>

      {/*   */}
      <motion.div
        layout //     
        className="projects-grid"
      >
        <AnimatePresence mode="popLayout">
          {filteredProjects.map(project => (
            <motion.div
              key={project.id}
              layout
              variants={cardVariants} //  variant 
              initial="hidden" //  
              whileInView="visible" //     
              exit="exit" //    
              viewport={{ once: false, amount: 0.2 }} //  , 20%   
            >
              <ProjectItem
                title={project.title}
                description={project.description}
                image={images[project.imageFileName] || ''}
                tech={project.tech}
                date={project.date}
                link={project.link}
              />
            </motion.div>
          ))}
        </AnimatePresence>
      </motion.div>
    </Section>
  )
}

export default ProjectsSection
